<!--
 * @Author Demon3443002624@qq.com
 * @Date 2022-4-21 10:49:46
 * @Description 设备-机组监控-机组轨迹
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-18 16:11:37
 * @Version 2022-5-5 10:22:41 接口调试
 * @Version 2022-6-10 11:47:20 中心点仍然为终点，但是需要显示当前位置（最后一次记录的位置）并且信息窗口显示当前位置
-->
<template>
  <div>
    <BMainTopComponent />
    <el-container>
      <el-main>
        <div :id="id">
          <!-- <toggle-crew></toggle-crew> -->
          <el-row type="flex" align="middle" class="time-fixed-box">
            <el-col :span="20" class="no-warp">
              <span>筛选:</span>
              <el-date-picker
                v-model="beginDate"
                type="date"
                placeholder="选择日期"
                :picker-options="pickerOptions"
                placement="bottom-start"
              >
                >
              </el-date-picker>
              <el-date-picker
                v-model="endTime"
                type="datetime"
                placeholder="选择结束时间"
                format="yyyy-MM-dd HH:mm"
                placement="bottom-start"
              >
              </el-date-picker>
              <el-button class="search-btn" type="primary" size="mini" @click="handleQuery"
                >搜索
              </el-button>
            </el-col>

            <el-col :span="4"></el-col>
          </el-row>
          <el-button
            @click="handleBack"
            title="返回机组当前位置"
            class="back-to-center"
            v-show="backBtnShow"
            icon="el-icon-refresh-left"
          ></el-button>
          <div
            :id="id + '-map-box'"
            v-loading="loading"
            :element-loading-text="loadingMsg"
            element-loading-background="rgba(0, 0, 0, 0.6)"
          ></div>
        </div>
      </el-main>
      <toggle-crew></toggle-crew>
    </el-container>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'

// import _data from './data'
import loadBMap from '@/utils/loadBMap'
import { formatTime } from '@/utils/common.js'
import ToggleCrew from '@/components/toggleCrew/list'

export default {
  name: 'crew-location',
  components: {
    ToggleCrew
  },
  mounted() {
    this.renderBMap()
  },
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        }
      },
      id: `crew-location`,
      dialogVisible: false,
      // 电子围栏的半径,默认500米
      fenceRadius: 500,
      // 电子围栏的中心点，默认当前位置
      fenceCenterPoint: null,
      beginDate: this.$moment().add(-3, 'days').format(`YYYY-MM-DD`),
      endTime: this.$moment().format(`YYYY-MM-DD HH:mm`),
      dataObj: null,
      bMap: null,
      fenceMap: null,
      centerPoint: null,
      infoWindow: null,
      loading: false,
      loadingMsg: '地图加载中，请您稍候...',
      backBtnShow: false
    }
  },
  watch: {
    selectCrew(newVal, oldVal) {
      this.renderBMap()
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  methods: {
    // 渲染：轨迹地图
    renderBMap() {
      this.loadingMsg = '地图加载中，请您稍候...'
      this.loading = true
      loadBMap()
        .then((e) => {
          this.$api.equipment
            .findAllGeneratroSetOrbitRecordV3(
              {
                model: {
                  StartTime: this.beginDate,
                  EndTime: this.endTime,
                  Title: '暂无机组轨迹'
                }
              },
              {
                // generatorSetId: 7297
                generatorSetId: this.selectCrew.ID || 0
              }
            )
            .then((res) => {
              console.log('res', res)
              this.loading = false
              this.dataObj = res.Data
              // this.dataObj = _data.track.Data
              this.centerPoint = null
              const BMap = e || undefined
              // [x] 此处百度地图设置的 size 必须和 icon 图标的大小一致，百度地图只是绘制了一个 size 大小的容器，然后将图标原封不动得放进去而已
              const size = new BMap.Size(48, 48)
              const map = new BMap.Map(`${this.id}-map-box`, {
                enableMapClick: false
              })
              const dataObj = this.dataObj
              const GensetMap = dataObj.GensetMap
              const OrbitList = dataObj.OrbitList
              const [x, y] = GensetMap ? [GensetMap.Longitude, GensetMap.Latitude] : []
              // 当前定位
              const centerPoint = new BMap.Point(x, y)
              this.centerPoint = centerPoint
              const marker = new BMap.Marker(centerPoint)
              const opts = {
                width: 300,
                height: 100,
                title: `<div style='font-size:1.2rem;font-weight:600'>${GensetMap.DisplayName}</div>`,
                offset: new BMap.Size(0, -10)
              }
              const infoWindow = new BMap.InfoWindow(
                `<el-card class="box-card">
                    <div  class="text item">
                        经度：${x}
                    </div>
                    <div  class="text item">
                        纬度：${y}
                    </div>
                    <div  class="text item">
                        ${GensetMap.InstallationSite}
                    </div>
                  </el-card>
                  `,
                opts
              )
              this.infoWindow = infoWindow
              marker.addEventListener(`click`, (_) => {
                map.openInfoWindow(infoWindow, centerPoint)
              })
              // map.openInfoWindow(infoWindow, centerPoint)

              // map.centerAndZoom(centerPoint, 16)
              map.addOverlay(marker)
              map.enableScrollWheelZoom()
              if (OrbitList && OrbitList.length > 0) {
                const polylineList = []
                OrbitList.forEach((e, index) => {
                  const [x, y] = [e.Longitude, e.Latitude]
                  const point = new BMap.Point(x, y)
                  const opts = {
                    width: 300,
                    height: 100,
                    title: `<div style='font-size:1.2rem;font-weight:600'>${formatTime(
                      e.AcquisitionTime
                    ).format(`YYYY年M月D日 HH:mm:ss`)}</div>`,
                    offset: new BMap.Size(0, 0)
                  }
                  const infoWindow = new BMap.InfoWindow(
                    `<el-card class="box-card">
                    <div  class="text item">
                        经度：${x}
                    </div>
                    <div  class="text item">
                        纬度：${y}
                    </div>
                  </el-card>
                  `,
                    opts
                  )
                  if (index === 0) {
                    // 开始图标
                    const iconBegin = new BMap.Icon(
                      require('@/assets/img/crewLocation/beginIcon48.png'),
                      size
                    )
                    const markerBegin = new BMap.Marker(point, {
                      icon: iconBegin
                    })
                    markerBegin.addEventListener('click', function () {
                      map.openInfoWindow(infoWindow, point) // 开启信息窗口
                    })
                    map.addOverlay(markerBegin)
                  } else if (index === OrbitList.length - 1) {
                    // 结束图标
                    const iconEnd = new BMap.Icon(
                      require('@/assets/img/crewLocation/endIcon48.png'),
                      size
                    )
                    const markerEnd = new BMap.Marker(point, { icon: iconEnd })
                    markerEnd.addEventListener('click', function () {
                      map.openInfoWindow(infoWindow, point) // 开启信息窗口
                    })
                    // 中心点设置为最后的位置
                    // this.centerPoint = point
                    map.addOverlay(markerEnd)
                  } else {
                    // 中间点的图标
                    const icon = new BMap.Icon(
                      require('@/assets/img/crewLocation/locationIcon.png'),
                      new BMap.Size(16, 26)
                      // {anchor: new BMap.Size(-2, 0)}
                    )
                    const marker = new BMap.Marker(point, { icon: icon })
                    marker.addEventListener(`click`, () => {
                      map.openInfoWindow(infoWindow, point)
                    })
                    map.addOverlay(marker)
                  }
                  polylineList.push(point)
                })
                const polyline = new BMap.Polyline(polylineList, {
                  strokeColor: '#1ecfef',
                  strokeWeight: 2,
                  strokeOpacity: 0.5,
                  strokeStyle: `solid`,
                  enableEditing: false
                }) // 创建折线
                map.addOverlay(polyline) // 增加折线
                map.addEventListener(`movestart`, (_) => {
                  this.backBtnShow = true
                })
                this.bMap = map
              }
              if (!this.centerPoint) {
                this.centerPoint = new BMap.Point(103, 31)
                map.centerAndZoom(this.centerPoint, 5)
              } else {
                map.openInfoWindow(infoWindow, this.centerPoint)
                map.centerAndZoom(this.centerPoint, 16)
              }
            })
        })
        .catch((err) => {
          alert(err)
        })
    },
    // 按钮：根据开始结束时间查询
    handleQuery() {
      // 验证时间有效性
      const flag = this.$moment(this.endTime).diff(this.$moment(this.beginDate), 'seconds')
      if (flag > 0) {
        this.renderBMap()
      } else {
        this.$message({
          message: '开始日期必须在结束时间之前！请检查。',
          type: 'warning'
        })
      }
    },
    // 按钮：地图恢复初始状态
    handleBack() {
      // 有动画的移动
      this.bMap.closeInfoWindow()
      if (!this.centerPoint) {
        this.bMap.panTo(this.centerPoint)
        this.bMap.openInfoWindow(this.infoWindow, this.centerPoint)
      }
      this.backBtnShow = false
    }
  }
}
</script>
<style lang="less" scoped>
#crew-location {
  display: undefined;
  // 百分比占比盒子
  .box100percent {
    width: 100%;
    height: 100%;
  }
  // 强制盒子子元素不换行，若不生效，需尝试子元素添加 display:inline; 属性
  .no-warp {
    white-space: nowrap;
  }
  // 搜索按钮
  .search-btn {
    height: 32px;
    // width: 60px;
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    // background: #fff;
    // border: 1px solid #dcdfe6;
    // color: #606266;
    text-align: center;
    box-sizing: border-box;
    outline: 0;
    margin: 0;
    transition: 0.1s;
    font-weight: 500;
    font-size: 14px;
    border-radius: 4px;
    // &:hover {
    //   background-color: #ecf5ff;
    //   color: #409eff;
    // }
  }
  .box100percent();
  // position: absolute;
  &-map-box {
    .box100percent();
  }
  // 电子围栏开关盒子
  .fence-fixed-box {
    // position: absolute;
    width: 260px;
    height: 32px;
    background-color: transparent;
    border: 1px solid #80808066;
    border-radius: 4px;
    display: inline-flex;
    // top: 208px;
    // left: 40px;
  }
  // 时间筛选
  .time-fixed-box {
    width: 100%;
    // margin: -24px 0 0 0px;
    padding: 0 0 10px 15px;
  }
  // 电子围栏地图
  &-fence-box {
    height: 500px;
  }
  // 返回当前机组定位按钮
  .back-to-center {
    width: 80px;
    position: absolute;
    right: 20px;
    top: 240px;
    z-index: 2;
    background: transparent;
    border: 0;
    font-size: 2.3rem;
  }
  .current-radius {
    position: absolute;
    z-index: 2;
    top: 3px;
    left: 5px;
  }
  /** 对组件样式的复写 */
  // 去除 dialog 的padding
  /deep/ .el-dialog__body {
    padding: 0;
  }
  // 去除底部的百度地图版权信息
  /deep/ .BMap_cpyCtrl {
    display: none;
  }
  /deep/ .anchorBL {
    display: none;
  }
  /deep/ .BMap_vectex_node {
    cursor: all-scroll;
    background: #ff0000;
    border-radius: 2px;
  }
}
</style>



